import React from 'react';

require('./css/');

class AppDownload extends React.Component {
  componentDidMount() {
  }
  close(){
    console.log('dgdgdgdgd');
  }
  render() {
    return (
      <div className='appDownload posRelative floatL' ref='Close'>
        <img src='../img/freeService_04.jpg' />
        <span className='close' onClick={this.close}>&nbsp;</span>
        <span className='download'>&nbsp;</span>
      </div>
    )
  }
}

export default AppDownload



